/*back to top*/
.back-to-top {
  position: fixed;
  right: 0;
  width: 5em;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity 300ms linear; }
  .back-to-top:before {
    font-family: FontAwesome;
    content: '\f062';
    height: 3rem;
    width: 100%;
    margin-top: -3rem; }
  .back-to-top i.fa {
    font-size: 3rem; }
  .back-to-top span {
    display: block;
    font-size: 1.2rem; }
    .back-to-top span::selection {
      background-color: transparent; }
  .back-to-top.back-to-top--scrolled {
    bottom: 5vh;
    cursor: pointer;
    visibility: visible;
    opacity: .5;
    transition: opacity 300ms linear; }
    .back-to-top.back-to-top--scrolled:hover {
      opacity: 1;
      transition: opacity 300ms linear; }

